'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-read-only-property';


// MAIN //

/**
* Namespace.
*
* @namespace ns
*/
var ns = {};

/**
* @name BooleanVector
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/ndarray/vector/bool}
*/
import BooleanVector from '@stdlib/ndarray-vector-bool';
setReadOnly( ns, 'BooleanVector', BooleanVector );

/**
* @name vector
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/ndarray/vector/ctor}
*/
import vector from '@stdlib/ndarray-vector-ctor';
setReadOnly( ns, 'vector', vector );

/**
* @name Float32Vector
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/ndarray/vector/float32}
*/
import Float32Vector from '@stdlib/ndarray-vector-float32';
setReadOnly( ns, 'Float32Vector', Float32Vector );

/**
* @name Float64Vector
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/ndarray/vector/float64}
*/
import Float64Vector from '@stdlib/ndarray-vector-float64';
setReadOnly( ns, 'Float64Vector', Float64Vector );

/**
* @name Int8Vector
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/ndarray/vector/int8}
*/
import Int8Vector from '@stdlib/ndarray-vector-int8';
setReadOnly( ns, 'Int8Vector', Int8Vector );

/**
* @name Int16Vector
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/ndarray/vector/int16}
*/
import Int16Vector from '@stdlib/ndarray-vector-int16';
setReadOnly( ns, 'Int16Vector', Int16Vector );

/**
* @name Int32Vector
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/ndarray/vector/int32}
*/
import Int32Vector from '@stdlib/ndarray-vector-int32';
setReadOnly( ns, 'Int32Vector', Int32Vector );

/**
* @name Uint8Vector
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/ndarray/vector/uint8}
*/
import Uint8Vector from '@stdlib/ndarray-vector-uint8';
setReadOnly( ns, 'Uint8Vector', Uint8Vector );

/**
* @name Uint8ClampedVector
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/ndarray/vector/uint8c}
*/
import Uint8ClampedVector from '@stdlib/ndarray-vector-uint8c';
setReadOnly( ns, 'Uint8ClampedVector', Uint8ClampedVector );

/**
* @name Uint16Vector
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/ndarray/vector/uint16}
*/
import Uint16Vector from '@stdlib/ndarray-vector-uint16';
setReadOnly( ns, 'Uint16Vector', Uint16Vector );

/**
* @name Uint32Vector
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/ndarray/vector/uint32}
*/
import Uint32Vector from '@stdlib/ndarray-vector-uint32';
setReadOnly( ns, 'Uint32Vector', Uint32Vector );


// EXPORTS //

export default ns;
